import React, { useState } from 'react';
import styles from './Notificacoes.module.css';
import { FaBell, FaCheckCircle, FaTimesCircle } from 'react-icons/fa';

const Notificacoes: React.FC = () => {
  const [notifications, setNotifications] = useState([
    { id: 1, title: 'Nova Reserva', message: 'Uma nova reserva foi realizada', read: false },
    { id: 2, title: 'Novo Comentário', message: 'Alguém comentou na sua reserva.', read: true },
    { id: 3, title: 'Alteração de Ambiente', message: 'O ambiente foi alterado.', read: false },
  ]);

  const markAsRead = (id: number) => {
    setNotifications((prevNotifications) =>
      prevNotifications.map((notification) =>
        notification.id === id ? { ...notification, read: true } : notification
      )
    );
  };

  return (
    <div className={styles.container}>
      <h1>Notificações</h1>
      <ul className={styles.notificationList}>
        {notifications.map((notification) => (
          <li key={notification.id} className={notification.read ? styles.read : ''}>
            <div>
              <h3>{notification.title}</h3>
              <p>{notification.message}</p>
            </div>
            <div className={styles.actions}>
              {!notification.read && (
                <button onClick={() => markAsRead(notification.id)} className={styles.markAsRead}>
                  <FaCheckCircle /> Marcar como lida
                </button>
              )}
              <button className={styles.delete}>
                <FaTimesCircle /> Excluir
              </button>
            </div>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default Notificacoes;
